.home{
    color: white;
    line-height: 8vh;
    width: 100%;
    height: 90%;
    position: relative;
    display: flex;
    flex-wrap: wrap;
}
.homedes{
    height: auto;
    width: auto;
    position: absolute;
    left: 15%;
    top: 120px;
}
.homedes h2{
    font-size: 190%;
    width: max-content;
}
.homedes h4{
    font-size: 140%;
    width: max-content;
}
.homedes h4 strong{
    color: rgb(106, 222, 222);
}
.homebutton a{
    color: white;
    padding: 10px 25px;
    margin-right:8px;
    border: 2px solid white;
    border-radius: 20px;
    transition: .5s ease-in;
}
.homeimg{
    width: 30%;
    height: 60vh;
    position: absolute;
    right: 13%;
    top: 60px;
    
}
.homeimg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 2px solid rgb(106, 222, 222);
    padding: 5px;
    box-shadow: 0 0 10px .1rem rgb(106, 222, 222);
}
.homedes>h2:hover,.homedes>h4:hover{
    color: rgb(106, 222, 222);
}
.homebutton a:hover{
    background-color: rgb(106, 222, 222);
    color: #000;
    box-shadow: 0 0 10px .1rem rgb(106, 222, 222);
}

@media only screen and (max-width: 450px){
    .homeimg{
        width: 100%;
        height: auto;
    }
    .homeimg img{
        width: 50%;
        height: 18%;
        position: relative;
        left: 40px;
    }
    .home{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        flex-direction: column;
    }
    .homedes{
        height: 100%;
        width: 100%;
        text-align: center;
        position: relative;
        top: 320px;
        line-height: 50px;
    }
    .homebutton{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        
    }
    .homebutton a{
        width: 80%;
        height: 7vh;
        margin-top: 10px;
    }
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (min-width: 451px) and (max-width: 680px){
    .homeimg{
        width: 25%;
        height: 35vh;
        position: absolute;
        left: 8%;
        top: 30px;
        
    }
    .home{
        display: flex;
        flex-direction: column;
    }
    .homedes{
        height: auto;
        width: auto;
        position: absolute;
        left: 8%;
        top: 300px;
    }
  }
  
  /* Small devices (portrait tablets and large phones, 600px and up) */
  @media only screen and (min-width: 681px) and (max-width:767px) {
    .homeimg{
        width: 20%;
        height: 40vh;
        position: absolute;
        right: 8%;
        top: 60px;
        
    }
  }
  
  /* Medium devices (landscape tablets, 768px and up) */
  @media only screen and (min-width: 768px) and (max-width:991px) {
    .home{
        width: 100%;
        height: 100%;
    }
    .homeimg{
        width: 100%;
        height: 100%;
        position: relative;
    }
    .homeimg img{
        width: 40%;
        height: 15rem;
        position: absolute;
        right: 15%;
        top: 1px;
    }
    .homedes{
        position: absolute;
        top: 300px;
        left: 250px;
    }
  } 